body {
  margin: 0;
  padding: 0;
  padding-bottom: 2rem;
  font-family: 'Roboto', 'Future',-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

.nav-scroller {
  position: relative;
}

.nav {
 position: absolute;
 top: 3px;
 right:16px;
}

.nav-scroller {
  margin-top: 2rem;
}

h3 {
  color: #3F4C50;
  font-size: 1.5rem;
  font-weight: 800;
}

/* News Items */

#news-container {
  border-bottom: 2px solid #f1f1f1;
}

#news-content {
  position: relative;
}

.news-title {
  text-align: left;
  margin-top: 1.5rem;
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
  color: #3F4C50;
}

/* Carousel Elements */

.slider {
  margin-bottom: 2rem;
}

.slick-slide img {
  opacity: .4;
}

.slick-slide .slick-center {
  opacity:1;
}


.slick-slide.slick-center img {
  opacity: 1;
  transform: scale(1.25);
}

.slick-dots {
  bottom: -65px;
}

.slick-dots li button:before {
  font-size: 12px;
  line-height: 20px;
}

.carousel-item {
  position: relative;
}

.slide__caption {
  color: #F8F8F8;
  font-weight: bold;
  top:10px;
  position: absolute;
  width: 75%;
  z-index: 2;
}


/* Orange Bars */
hr {
  background: #E08D4F;
  height  : .5rem;
}


.shortBar {
  margin-top:2rem;
  width: 15%;
}

.longBar {
  margin-top:5rem;
  margin-bottom: 2rem;
}

/* Text on page adjustments */

a {
  color: white;
}

.card img {
  width: 100%;
  height: 15vw;
  object-fit: cover;
}

.about-text {
  color:white;
  margin-top:2rem;
}

label {
  color: white;
}

.card {
  margin-top:3rem;
}

.logo-img {
  margin-bottom: 1em;
  margin-top:1.425em;
}

.full-coverage-button {
  position: absolute;
  right: 0;
  bottom: 1rem;
}

.btn-warning {
  background-color: #E08D4F !important;
}

#gray-container {
  background-color: #F8F8F8;
}

#white-container, #gray-container {
  padding-top: 2rem;
  padding-bottom: 2rem;
}

#landing-background {
  background-image: url("img/lukas-800847-unsplash.jpg");
  margin-top: 2rem;
  background-repeat: no-repeat; 
  padding: 15rem;
  background-size: 1366px 796px;
}

.landing-title, .description-body {
  font-family: "Noto Sans", sans-serif;
}

.landing-title {
  font-weight: 800;
}

.description-body {
  text-align: center;
  font-size: 1.2rem;
}

#intro {
  position: relative;
  bottom:120px;
}

@media (max-width: 900px) {
  .display-1 {
    font-weight: 500;
    font-size: 4rem;
  }
  h3 {
    font-size: 1.5rem;
  }
}

@media (max-width: 600px) {
  h3 {
    font-size: 1rem;
  }
}
